@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@600&family=Roboto:wght@300&display=swap');

$font-1: 'Roboto', sans-serif;
$font-2: 'Josefin sans' serif;

$color-1: #2a1d52;
$color-2: #6C77Fa;
$color-3: rgb(99,164,233);
$color-4: #4FEDD2;
$color-5: #36D0EA;
$white: #2a1d52;
$black: #2a1d52;

$borderRadius: 25px;
$shadow: 0 1px 3px #333;
$min-height: 95vh;

@mixin verticalCenter {
    display: flex;
    justify-content: center;
    align-items: center;
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    margin: 1rem;
    max-width: 1200px;
    font-family: $font-2;
    color: $black;
}

button {
    cursor: pointer;
}

li {
    list-style-type: none;
}

a {
    text-decoration: none;
    color: $black;
    cursor: pointer;
}

a:visited {
    color: $black;
}

.home, .knowledges, .portfolio, .contact {
    display: flex;

    @media screen and (max-width: 850px){
        display: block !important;
    }
}

.homeContent, .knowledgesContent, portfolioContent, .contactContent {
    margin-left: .4rem;

    @media screen and (max-width: 850px){
        margin-left: 0 !important;
    }
} 


