@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@600&family=Roboto:wght@300&display=swap);
*{box-sizing:border-box;margin:0;padding:0}body{margin:1rem;max-width:1200px;font-family:"Josefin sans" serif;color:#2a1d52}button{cursor:pointer}li{list-style-type:none}a{text-decoration:none;color:#2a1d52;cursor:pointer}a:visited{color:#2a1d52}.home,.knowledges,.portfolio,.contact{display:flex}@media screen and (max-width: 850px){.home,.knowledges,.portfolio,.contact{display:block !important}}.homeContent,.knowledgesContent,portfolioContent,.contactContent{margin-left:.4rem}@media screen and (max-width: 850px){.homeContent,.knowledgesContent,portfolioContent,.contactContent{margin-left:0 !important}}.sideBar{background:#2a1d52;font-family:"Roboto",sans-serif;height:100%;min-height:95vh;width:264px;border-radius:25px;position:relative;display:grid;grid-template-columns:1fr;grid-template-rows:280px 1fr 108px;grid-template-areas:"1" "2" "3"}.sideBar .id{grid-area:1;text-align:center;-webkit-user-select:none;-ms-user-select:none;user-select:none;position:relative}.sideBar .idContent{position:absolute;bottom:0;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%);width:100%}.sideBar .idContent img{border-radius:50%;box-shadow:0 0 0 2px #63a4e9,0 0 0 4px #4FEDD2}.sideBar .idContent h3{font-size:1.5rem;color:#36D0EA;letter-spacing:.2rem;margin-top:1.5rem}.sideBar .navigation{grid-area:2;padding:2rem 1rem}.sideBar .navigation ul{height:100%;display:grid}.sideBar .navigation ul li{padding:1rem 0 1rem .4rem;font-size:1.3rem;position:relative}.sideBar .navigation ul li:hover i{-webkit-transform:translate(3px, 3px);transform:translate(3px, 3px)}.sideBar .navigation ul li a{color:#36D0EA}.sideBar .navigation ul li .navActive{color:#4FEDD2}.sideBar .navigation ul li i{position:absolute;font-size:0.95rem;-webkit-transform:translateY(3px);transform:translateY(3px);transition:.2s ease}.sideBar .navigation ul li span{margin-left:2.7rem}.sideBar .socialNetwork{grid-area:3}.sideBar .socialNetwork ul{display:flex;width:128px;padding:0 14px;justify-content:space-between}.sideBar .socialNetwork ul li{font-size:1.5rem;background:#36D0EA;color:#2a1d52;border-radius:50%;padding:.5rem 0.8rem;transition:.3s;cursor:pointer}.sideBar .socialNetwork ul li a:visited{color:#2a1d52}.sideBar .socialNetwork ul li:hover{background:#4FEDD2}.sideBar .signature{margin-top:1rem;margin-bottom:1rem;text-align:center;letter-spacing:0.1rem;font-size:.7rem}.sideBar .signature p{color:#36D0EA}.sideBar .signature p:hover{color:#4FEDD2}@media screen and (max-width: 850px){.sideBar{display:flex;min-height:50px;margin-bottom:0.2rem;width:100%}.sideBar .id,.sideBar .socialNetwork,.sideBar .signature{display:none}.sideBar .navigation{width:100%;padding:.1rem 1rem}.sideBar .navigation ul{display:flex;justify-content:space-between}.sideBar .navigation ul span{margin-left:1.5rem !important}}@media screen and (max-width: 580px){.sideBar ul{justify-content:space-around !important}.sideBar li{padding:1rem 0 !important}.sideBar i{font-size:1.4rem;-webkit-transform:translateY(-3px);transform:translateY(-3px)}.sideBar span{display:none}}

